// src/pages/retailer/stock.js
import React, { useEffect, useState } from 'react';
import RetailerLayout from '../../components/RetailerLayout';
import { Button } from "@/components/ui/button";
import { useAuthGuard } from '../../hooks/useAuthGuard';
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Pagination,
  PaginationContent,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/ui/pagination";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertTriangle, Loader2, Check } from "lucide-react";

// Helper to format currency
const formatPrice = (p) => `â‚¹${Number(p || 0).toLocaleString('en-IN')}`;

// Helper to get total stock
function totalStockFrom(product = {}) {
  if (Array.isArray(product.sizes)) {
    return product.sizes.reduce((acc, it) => acc + (Number(it.stock || 0)), 0);
  }
  return product.totalStock || 0;
}

const PRODUCTS_PER_PAGE = 12;

export default function StockFromWholesalerPage() {
    const { isLoading: isAuthLoading } = useAuthGuard("RETAILER");
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [page, setPage] = useState(1);
  const [totalProducts, setTotalProducts] = useState(0);

  // --- NEW: State for stocking ---
  const [stockingId, setStockingId] = useState(null); // ID of product being stocked
  const [stockedProductIds, setStockedProductIds] = useState(new Set()); // IDs of products already stocked

  const totalPages = Math.ceil(totalProducts / PRODUCTS_PER_PAGE);

  const fetchWholesaleProducts = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const token = localStorage.getItem("token"); //
      if (!token) throw new Error("No authorization token found. Please log in again.");

      const authHeaders = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}` //
      };

      const res = await fetch(
        `/api/retailer/wholesale-products?page=${page}&limit=${PRODUCTS_PER_PAGE}`, 
        { headers: authHeaders }
      );
      
      if (!res.ok) {
         const errData = await res.json();
         throw new Error(errData.error || 'Failed to fetch products');
      }
      const data = await res.json();
      setProducts(data.items);
      setTotalProducts(data.total);
    } catch (err) {
      console.error(err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (isAuthLoading) return;
    fetchWholesaleProducts();
  }, [page, isAuthLoading]); // Re-fetch when page changes

  if (isAuthLoading) {
  return (
    <RetailerLayout>
      <div className="flex justify-center items-center py-10">
        <Loader2 className="h-8 w-8 animate-spin" />
        <p className="ml-2">Verifying access...</p>
      </div>
    </RetailerLayout>
  );
}

  const handlePageChange = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages) {
      setPage(newPage);
    }
  };

  // --- NEW: Handle Stocking a Product ---
  const handleStockProduct = async (wholesaleProductId) => {
    setStockingId(wholesaleProductId);
    setError(null);

    try {
      const token = localStorage.getItem("token");
      if (!token) throw new Error("No authorization token found.");

      const res = await fetch('/api/retailer/stock-product', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ wholesaleProductId })
      });

      if (!res.ok) {
        const errData = await res.json();
        if (res.status === 409) { // 409 Conflict (already stocked)
          setStockedProductIds(prev => new Set(prev).add(wholesaleProductId));
        }
        throw new Error(errData.error || "Failed to stock product");
      }

      // Success! Mark as stocked
      setStockedProductIds(prev => new Set(prev).add(wholesaleProductId));

    } catch (err) {
      console.error(err);
      setError(err.message);
    } finally {
      setStockingId(null);
    }
  };

  return (
    <RetailerLayout>
      <div className="mb-6">
        <h1 className="text-3xl font-bold">Stock from Wholesaler</h1>
        <p className="text-muted-foreground">
          Browse products from all wholesalers and add them to your store.
        </p>
      </div>

      {loading && (
        <div className="flex justify-center items-center py-10">
          <Loader2 className="h-8 w-8 animate-spin" />
        </div>
      )}
      
      {error && (
        <Alert variant="destructive" className="mb-6">
          <AlertTriangle className="h-4 w-4" />
          <AlertTitle>Error</AlertTitle>
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {/* --- Products Grid --- */}
      {!loading && !error && products.length > 0 && (
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
          {products.map(product => {
            const isStocking = stockingId === product._id;
            const isStocked = stockedProductIds.has(product._id);
            
            return (
              <Card key={product._id}>
                <CardHeader className="pb-2">
                  <CardTitle className="text-lg">{product.name}</CardTitle>
                  <CardDescription>{formatPrice(product.price)} (Wholesale Price)</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="h-[150px] w-full rounded-md bg-gray-100 flex items-center justify-center mb-4">
                    <img
                      src={product.images && product.images[0] ? product.images[0] : "/images/placeholder.png"}
                      alt={product.name}
                      className="h-full w-full object-cover rounded-md"
                    />
                  </div>
                  <p className="text-sm text-muted-foreground line-clamp-2">
                    {product.description || "No description."}
                  </p>
                </CardContent>
                <CardFooter className="flex justify-between items-center">
                  <div className="text-sm text-muted-foreground">
                    <span className="font-medium text-black">{totalStockFrom(product)}</span>
                    <span className="ml-1">in stock</span>
                  </div>
                  <Button
                    onClick={() => handleStockProduct(product._id)}
                    disabled={isStocking || isStocked}
                  >
                    {isStocking ? (
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    ) : isStocked ? (
                      <Check className="mr-2 h-4 w-4" />
                    ) : null}
                    {isStocking ? "Stocking..." : (isStocked ? "Stocked" : "Stock This Item")}
                  </Button>
                </CardFooter>
              </Card>
            );
          })}
        </div>
      )}
      
      {!loading && !error && products.length === 0 && (
        <Card>
          <CardContent className="pt-6">
            <p className="text-center text-muted-foreground">
              No products found from any wholesalers.
            </p>
          </CardContent>
        </Card>
      )}
      
      {/* Pagination Controls */}
      {totalPages > 1 && (
        <Pagination className="mt-6">
          <PaginationContent>
            <PaginationItem>
              <PaginationPrevious 
                href="#"
                onClick={(e) => { e.preventDefault(); handlePageChange(page - 1); }}
                disabled={page === 1}
              />
            </PaginationItem>
            <PaginationItem>
              <PaginationLink href="#" isActive>
                {page}
              </PaginationLink>
            </PaginationItem>
            <PaginationItem>
              <PaginationNext 
                href="#"
                onClick={(e) => { e.preventDefault(); handlePageChange(page + 1); }}
                disabled={page === totalPages}
              />
            </PaginationItem>
          </PaginationContent>
        </Pagination>
      )}
    </RetailerLayout>
  );
}